<template>
  <div class="Project">
    <div class="ProjectContainer">
      <!-- Introduce -->
      <div id="Introduce" class="Center ContainerFluid">
        <h1>My projects</h1>
        <p>
          All my complete open-source projects. I using GitHub to push my
          project into that
        </p>
      </div>

      <!-- Flexblock -->
      <div class="FlexBlock">
        <!-- Left flex -->
        <div class="DirectoryList">
          <ul>
            <li v-for="(value, index) in dataList" :key="index">
              {{ value.name }}

              <ul v-if="value.data.length > 0">
                <li v-for="(subValue, subIndex) in value.data" :key="subIndex">
                  <a href="#">{{ subValue.name }}</a>
                </li>
              </ul>
            </li>
          </ul>
        </div>
        <!-- Right flex -->
        <div>
          <div class="Container" v-for="(item, index) in dataList" :key="index">
            <h1 class="ContainerTitle">&lt;!-- {{ item.name }} --&gt;</h1>
            <div class="ContainerBody">
              <ProjectItemCard
                class="Card"
                v-for="(cardItem, cardIndex) in item.data"
                :key="cardIndex"
                :item="cardItem"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
// Item Card
import ProjectItemCard from "@/components/ProjectItemCard";

export default {
  title: "Project",
  name: "Project",
  components: {
    ProjectItemCard,
  },
  data() {
    return {
      dataList: [],
    };
  },
  mounted() {
    // Get project dat and set
    axios
      .get("asset/project-data.json")
      .then((response) => {
        this.dataList = response.data;
      })
      .catch((error) => {
        alert("Having an error while connect to the data: " + error);
      });
  },
};
</script>

